.table_button {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
th > .table_button {
  padding: 0 5px;
}
.table_sort_icon {
  margin-left: 5px;
  height: 15px;
}
.table_controls {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 10px;
}
.table_controls > * {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2px;
}
@media screen and (max-width: 480px) {
  .table_controls > * {
    width: 100%;
  }
}
.table_button svg,
.table_controls svg {
  color: #303030;
}
.table_per_page span {
  font-size: 0.8em;
  margin-left: 5px;
}
.table_nav {
  flex-grow: 1;
}
.table_nav span {
  font-size: 0.8em;
  margin: 0 5px;
}
.table_nav_button {
  width: 25px;
  height: 25px;
  margin: 0;
  padding: 0;
}
.table_per_page .table_input select {
  width: 80px;
  height: 25px;
  padding: 0 5px;
  appearance: none;
  cursor: pointer;
}
.table_controls .table_input {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  margin: 0;
}
.table_search .table_input input {
  box-sizing: border-box;
  width: 150px;
  height: 25px;
  padding-left: 5px;
  padding-right: 25px;
}
.table_controls .table_input > svg {
  position: absolute;
  right: 5px;
  pointer-events: none;
}
.table_controls .table_input > button {
  position: absolute;
  right: 0;
  width: 25px;
  height: 25px;
  margin: 0;
  padding: 0;
}
.table_controls .table_input > button:hover {
  background: none;
}
.table_search span {
  margin-left: 5px;
  font-size: 0.8em;
}
